import { getType } from 'typesafe-actions';
import { addSourceFailure, addSourceRequest, addSourceSuccess, cancelOnboarding, checkKorekutaCheckList, checkSourceKindCheckList, closeModal, openModal, updateClusterID, updateKorekutaCheckList, updateName, updateSourceKindCheckList, updateType, } from './actions';
export const defaultState = {
    type: '',
    typeValid: true,
    typeDirty: false,
    name: '',
    nameValid: true,
    nameDirty: false,
    clusterId: '',
    clusterIdValid: true,
    clusterIdDirty: false,
    sourceKindChecks: {
        'check-ocp-version': false,
        'check-operator-metering': false,
        'check-insights-client': false,
        'check-ansible-epel': false,
        'check-oc': false,
    },
    korekutaChecks: {
        'check-ocp-api': false,
        'check-ocp-metering-operator-token-path': false,
        'check-ocp-operator-metering-namespace': false,
        'check-super-user-password': false,
    },
    isOpen: false,
    apiStatus: 0 /* none */,
    apiErrors: null,
};
const areAllChecked = state => Object.keys(state).every(k => state[k]);
const setAll = (state, value) => {
    return Object.keys(state)
        .map(k => ({ [k]: value }))
        .reduce((acc, curr) => (Object.assign({}, acc, curr)));
};
export const stateKey = 'onboarding';
export function reducer(state = defaultState, action) {
    switch (action.type) {
        case getType(updateType):
            return Object.assign({}, state, { type: action.payload, typeValid: action.meta(action.payload), typeDirty: true });
        case getType(updateName):
            return Object.assign({}, state, { name: action.payload, nameValid: action.meta(action.payload), nameDirty: true });
        case getType(updateClusterID):
            return Object.assign({}, state, { clusterId: action.payload, clusterIdValid: action.meta(action.payload), clusterIdDirty: true });
        case getType(updateSourceKindCheckList):
            return Object.assign({}, state, { sourceKindChecks: Object.assign({}, state.sourceKindChecks, { [action.payload.item]: action.payload.value }) });
        case getType(updateKorekutaCheckList):
            return Object.assign({}, state, { korekutaChecks: Object.assign({}, state.korekutaChecks, { [action.payload.item]: action.payload.value }) });
        case getType(checkSourceKindCheckList):
            const skVal = areAllChecked(state.sourceKindChecks) ? false : true;
            return Object.assign({}, state, { sourceKindChecks: setAll(state.sourceKindChecks, skVal) });
        case getType(checkKorekutaCheckList):
            const kVal = areAllChecked(state.korekutaChecks) ? false : true;
            return Object.assign({}, state, { sourceKindChecks: setAll(state.korekutaChecks, kVal) });
        case getType(cancelOnboarding):
            return defaultState;
        case getType(openModal):
            return Object.assign({}, state, { isOpen: true });
        case getType(closeModal):
            return Object.assign({}, state, { isOpen: false });
        case getType(addSourceRequest):
            return Object.assign({}, state, { apiStatus: 1 /* inProgress */, apiErrors: null });
        case getType(addSourceFailure):
            return Object.assign({}, state, { apiStatus: 2 /* complete */, apiErrors: action.payload });
        case getType(addSourceSuccess):
            return Object.assign({}, state, { apiStatus: 2 /* complete */, apiErrors: null });
        default:
            return state;
    }
}
