import { getQueryForWidget, ocpDashboardDefaultFilters, ocpDashboardStateKey, } from './ocpDashboardCommon';
export const selectOcpDashboardState = (state) => state[ocpDashboardStateKey];
export const selectWidgets = (state) => selectOcpDashboardState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectOcpDashboardState(state).currentWidgets;
export const selectWidgetQueries = (state, id) => {
    const widget = selectWidget(state, id);
    return {
        previous: getQueryForWidget(widget, Object.assign({}, ocpDashboardDefaultFilters, { time_scope_value: -2 })),
        current: getQueryForWidget(widget, ocpDashboardDefaultFilters),
        tabs: getQueryForWidget(widget, Object.assign({}, ocpDashboardDefaultFilters, { resolution: 'monthly' })),
    };
};
