import { getType } from 'typesafe-actions';
import { addProviderFailure, addProviderRequest, addProviderSuccess, clearProviderFailure, getProvidersFailure, getProvidersRequest, getProvidersSuccess, } from './providersActions';
export const defaultState = {
    providers: null,
    providersError: null,
    providersFetchStatus: 0 /* none */,
};
export const stateKey = 'providers';
export function providersReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(addProviderRequest):
            return Object.assign({}, state, { providersFetchStatus: 1 /* inProgress */ });
        case getType(addProviderSuccess):
            return Object.assign({}, state, { providers: {
                    count: 1,
                    results: [action.payload],
                }, providersError: null, providersFetchStatus: 2 /* complete */ });
        case getType(addProviderFailure):
            return Object.assign({}, state, { providersError: action.payload, providersFetchStatus: 2 /* complete */ });
        case getType(clearProviderFailure):
            return Object.assign({}, state, { providersError: null });
        case getType(getProvidersRequest):
            return Object.assign({}, state, { providersFetchStatus: 1 /* inProgress */ });
        case getType(getProvidersSuccess):
            return Object.assign({}, state, { providers: action.payload, providersError: null, providersFetchStatus: 2 /* complete */ });
        case getType(getProvidersFailure):
            return Object.assign({}, state, { providers: null, providersError: action.payload, providersFetchStatus: 2 /* complete */ });
        default:
            return state;
    }
}
