import { addProvider as apiCreateProvider } from 'api/providers';
import { createAction, createAsyncAction, createStandardAction, } from 'typesafe-actions';
export const updateType = createStandardAction('onboarding/update/type')();
export const updateName = createStandardAction('onboarding/update/name')();
export const updateClusterID = createStandardAction('onboarding/update/clusterId')();
export const updateKorekutaCheckList = createStandardAction('onboarding/update/korekutaCheck')();
export const checkKorekutaCheckList = createAction('onboarding/checkAll/korekutaCheck');
export const updateSourceKindCheckList = createStandardAction('onboarding/update/sourceKindCheck')();
export const checkSourceKindCheckList = createAction('onboarding/checkAll/sourceKindCheck');
export const openModal = createAction('onboarding/modal/open');
export const closeModal = createAction('onboarding/modal/close');
export const cancelOnboarding = createAction('onboarding/cancel');
export const { request: addSourceRequest, success: addSourceSuccess, failure: addSourceFailure, } = createAsyncAction('onboarding/source/add/request', 'onboarding/source/add/success', 'onboarding/source/add/failure')();
export function addSource(request) {
    return (dispatch) => {
        dispatch(addSourceRequest());
        return apiCreateProvider(request)
            .then(response => {
            dispatch(addSourceSuccess(response.data));
        })
            .catch(err => {
            dispatch(addSourceFailure(err));
        });
    };
}
