import { getType } from 'typesafe-actions';
import { setWidgetTab } from './awsDashboardActions';
import { computeWidget, costSummaryWidget, storageWidget, } from './awsDashboardWidgets';
export const defaultState = {
    currentWidgets: [costSummaryWidget.id, computeWidget.id, storageWidget.id],
    widgets: {
        [costSummaryWidget.id]: costSummaryWidget,
        [computeWidget.id]: computeWidget,
        [storageWidget.id]: storageWidget,
    },
};
export function awsDashboardReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(setWidgetTab):
            return Object.assign({}, state, { widgets: Object.assign({}, state.widgets, { [action.payload.id]: Object.assign({}, state.widgets[action.payload.id], { currentTab: action.payload.tab }) }) });
        default:
            return state;
    }
}
