import { getCurrentUser as apiGetCurrentUser } from 'api/users';
import { createAsyncAction } from 'typesafe-actions';
export const { request: getUserRequest, success: getUserSuccess, failure: getUserFailure, } = createAsyncAction('users/get/request', 'session/get/success', 'session/get/failure')();
export const { request: getCurrentUserRequest, success: getCurrentUserSuccess, failure: getCurrentUserFailure, } = createAsyncAction('users/current/request', 'session/current/success', 'session/current/failure')();
export const getCurrentUser = () => {
    return (dispatch) => {
        dispatch(getCurrentUserRequest());
        return apiGetCurrentUser()
            .then(response => {
            dispatch(getCurrentUserSuccess(response.data));
        })
            .catch(err => {
            dispatch(getCurrentUserFailure(err));
        });
    };
};
