import { getType } from 'typesafe-actions';
import { setWidgetTab } from './ocpDashboardActions';
import { chargeSummaryWidget, cpuWidget, memoryWidget, } from './ocpDashboardWidgets';
export const defaultState = {
    currentWidgets: [chargeSummaryWidget.id, cpuWidget.id, memoryWidget.id],
    widgets: {
        [chargeSummaryWidget.id]: chargeSummaryWidget,
        [cpuWidget.id]: cpuWidget,
        [memoryWidget.id]: memoryWidget,
    },
};
export function ocpDashboardReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(setWidgetTab):
            return Object.assign({}, state, { widgets: Object.assign({}, state.widgets, { [action.payload.id]: Object.assign({}, state.widgets[action.payload.id], { currentTab: action.payload.tab }) }) });
        default:
            return state;
    }
}
