import axios from 'axios';
export const ocpReportTypePaths = {
    ["charge" /* charge */]: 'reports/charges/ocp/',
    ["cpu" /* cpu */]: 'reports/inventory/ocp/cpu/',
    ["limit" /* limit */]: 'reports/inventory/ocp/limit/',
    ["memory" /* memory */]: 'reports/inventory/ocp/memory/',
    ["tag" /* tag */]: 'tags/ocp/',
};
export function runReport(reportType, query) {
    const path = ocpReportTypePaths[reportType];
    const insights = window.insights;
    if (insights &&
        insights.chrome &&
        insights.chrome.auth &&
        insights.chrome.auth.getUser) {
        return insights.chrome.auth.getUser().then(() => {
            return axios.get(`${path}?${query}`);
        });
    }
    else {
        return axios.get(`${path}?${query}`);
    }
}
