import { getQuery } from 'api/awsQuery';
export const awsDashboardStateKey = 'awsDashboard';
export const awsDashboardDefaultFilters = {
    time_scope_units: 'month',
    time_scope_value: -1,
    resolution: 'daily',
    limit: 5,
};
export function getGroupByForTab(tab) {
    switch (tab) {
        case "services" /* services */:
            return { service: '*' };
        case "accounts" /* accounts */:
            return { account: '*' };
        case "regions" /* regions */:
            return { region: '*' };
        case "instance_type" /* instanceType */:
            return { instance_type: '*' };
        default:
            return {};
    }
}
export function getQueryForWidget(widget, filter = awsDashboardDefaultFilters) {
    const query = {
        filter,
        group_by: getGroupByForTab(widget.currentTab),
    };
    return getQuery(query);
}
