import axios from 'axios';
export function initApi({ version }) {
    axios.defaults.baseURL = `/r/insights/platform/cost-management/api/${version}/`;
    axios.interceptors.request.use(authInterceptor);
}
export function authInterceptor(reqConfig) {
    return Object.assign({}, reqConfig, { headers: Object.assign({}, reqConfig.headers) });
}
