import { stateKey } from './usersReducer';
export const selectUsersState = (state) => state[stateKey];
export const selectUsersMap = (state) => selectUsersState(state).byId;
export const selectUsersFetchStatusMap = (state) => selectUsersState(state).fetchStatus;
export const selectUsersErrorMap = (state) => selectUsersState(state).errors;
export const selectCurrentUser = (state) => {
    const currentUserMock = {
        uuid: 'currentUser',
        username: 'currentUser',
        email: 'currentUser@test.test',
    };
    return currentUserMock;
};
export const selectCurrentUserFetchStatus = (state) => selectUsersFetchStatusMap(state).get('current');
export const selectCurrentUserError = (state) => selectUsersErrorMap(state).get('current');
