import { getType } from 'typesafe-actions';
import { getCurrentUserFailure, getCurrentUserRequest, getCurrentUserSuccess, } from './usersActions';
export const defaultState = {
    byId: new Map(),
    errors: new Map(),
    fetchStatus: new Map(),
};
export const stateKey = 'users';
export function usersReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(getCurrentUserRequest):
            return Object.assign({}, state, { fetchStatus: new Map(state.fetchStatus).set('current', 1 /* inProgress */) });
        case getType(getCurrentUserSuccess):
            return Object.assign({}, state, { fetchStatus: new Map(state.fetchStatus).set('current', 2 /* complete */), byId: new Map(state.byId).set('current', action.payload), errors: new Map(state.errors).set('current', null) });
        case getType(getCurrentUserFailure):
            return Object.assign({}, state, { fetchStatus: new Map(state.fetchStatus).set('current', 2 /* complete */), errors: new Map(state.errors).set('current', action.payload) });
        default:
            return state;
    }
}
