import { stateKey } from './providersReducer';
export const selectProvidersState = (state) => state[stateKey];
// Add provider
export const selectAddProviderFetchStatus = (state) => selectProvidersState(state).providersFetchStatus;
export const selectAddProviderError = (state) => selectProvidersState(state).providersError;
// Get providers
export const selectProviders = (state) => selectProvidersState(state).providers;
export const selectProvidersFetchStatus = (state) => selectProvidersState(state).providersFetchStatus;
export const selectProvidersError = (state) => selectProvidersState(state).providersError;
